          handleScroll(){
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop ;// 滚动条偏移量
            if($(".member_head")>0){
              $(".member_head").offset().top-$(document).scrollTop()<$("#Heads").height()?this.TopClass=true:this.TopClass=false
            }else{

            }

